import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ArticlesPage from './pages/ArticlesPage';
import SinglePage from './pages/SinglePage';
import NotFoundPage from './pages/NotFoundPage';

import Navbar from './components/Navbar';

import './App.css';

const appURL = "https://company.overdrive.com/wp-json/wp/v2/";
const getPosts = `${appURL}posts`;

class App extends Component {
  state = {
    posts: []
  }

  componentDidMount() {
    let postURL = `${getPosts}`;
    fetch(postURL)
      .then(data => data.json())
      .then(data => {
        this.setState({
          posts: data
        })
      })
  }

  render() {
    return (
      <Router>
        <div className="App">
          <Navbar />
          <div className="page-body">
            <Switch>
              <Route path="/" component={HomePage} exact />
              <Route path="/about" component={AboutPage} />
              <Route path="/articles" render={(props) => <ArticlesPage {...props} posts={this.state.posts} />} exact />
              <Route path="/articles/:name" render={(props) => <SinglePage {...props} posts={this.state.posts} />} />
              <Route component={NotFoundPage} />
            </Switch>
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
